.root {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: 2.5rem;
  width: 2.5rem;
}

.empty {
  background-color: var(--light-blue);
}

.seen {
  background-color: var(--light-gray);
}

.visible {
  background-color: var(--white);
}

.unknown {
  background-color: var(--light-blue);
}
